import React, { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
import {
  Paper,
  Grid,
  Box,
  AppBar,
  Button,
  useScrollTrigger,
  TextField,
  Slide,
} from '@material-ui/core';
import axios from 'axios';

const Visit = () => {
  const [lst, setLst] = useState('');
  const [userid, setUserId] = useState('');
  const [comment, setComment] = useState('');
  useEffect(()=>{
    getApi();
  }, []);

  const requestApi= (body)=> {
    console.log("requestApi operated")
    axios.post('/api/visitor', body)
    .then(res => {
      // setCards(res.data)
      console.log(res.data, '@@@@requestAPI')
    })
    .catch(err => {
      console.log(err)
    })
  }

  const getApi = ()=> {
    axios.get('/api/visitor')
    .then(res => {
      setLst(res.data);
      console.log(res.data, '@@@@getAPI')
    })
    .catch(err => {
      console.log(err)
    })
  }

  return (
    <Grid>
      <h1> .</h1>
      <Link to = "/home"> </Link>
      <Grid>
        <form onSubmit={(e)=> {
          // 1. submit    ,  body request param 
          // 2.     
          e.preventDefault();
          let body = {
            userid:userid,
            comment: comment
          }
          requestApi(body);
        }}>
          <TextField onChange={(e)=>{setUserId(e.target.value)}} id="filled-basic" label="name" variant="filled" />
          <TextField onChange={(e)=>{setComment(e.target.value)}} id="filled-basic" label="comment" variant="filled" />
          <Button type="submit"></Button>
        </form>
      </Grid>
      <hr />
      <Grid>
        {lst.map((row) => (
          <Grid key = {row}>
            <Paper>
              <p>{row.userId}</p>
              <p>{row.comment}</p>
              <p>{row.logDate}</p>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};



export default Visit;